import * as React from 'react';
import createListItem from '../components/createListItem';
import { Block, RenderNodeProps } from '@ali/4ever-cangjie';
import { EditParagraph } from '@ali/4ever-plugin-paragraph';

export function createRenderParagraph(configs: Record<string, any>) {
  const Paragraph = createListItem(EditParagraph);

  return (_, props: RenderNodeProps<Block>) => {
    const { children, node } = props;
    if (!node.data?.list) {
      return null;
    }

    return (
      <Paragraph {...props} {...configs.paragraph}>
        {children()}
      </Paragraph>
    );
  };
}
